import React, { PureComponent, Fragment, createElement } from 'react';

import Modal from 'components/Modal'
import Image from 'components/Image'
import Text from 'components/Text'
import Box from 'components/Box'
import Button from 'components/Button'

import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import Rater from './Rater'
import rateHoc from './rateHoc'
import docStar from './doc-star.svg'

const Title = (props) => (
  <Text
    fontSize={mobileOrDesktop('1em', '1.5em')}
    color="titleBlue"
    fontWeight="bold"
    my="0.5em"
    {...props}
  />
)

class StarRater extends PureComponent {
  state = {}

  componentDidMount() {
    if (!this.props.score) {
      this.setState({ isOpen: true })
    }
  }

  saveRate = () => {
    this.props.saveRate(this.state.score)
    this.setState({ saved: true }, () => {
      setTimeout(() => this.setState({ isOpen: false }), 2000)
    })
  }

  setRate = (score) => this.setState({ score })

  onClose = () => this.setState({ isOpen: false })

  render() {
    const { isOpen, saved, score } = this.state
    const { title } = this.props
    return (
      <Modal
        py="1em"
        isOpen={isOpen}
        onRequestClose={this.onClose}
      >
        <Box textAlign="center" my={mobileOrDesktop('0.5em', '1em')}>
          <Box mx={mobileOrDesktop('15%', '30%')} mb="1em">
            <Image src={docStar} />
          </Box>
          {saved ? (
            <Fragment>
              <Title>謝謝你的回饋！</Title>
              <Text fontSize="1.25em">本跳窗將於兩秒後自動關閉</Text>
            </Fragment>
          ) : (
            <Fragment>
              <Title my="0" mx="0.75em">{title}</Title>
              {/* <Text fontSize="1.25em">給予評分讓我們能更優化你的看診前準備體驗</Text> */}
              <Rater justifyContent="center" onRate={this.setRate} my="2em" />
              <Button onClick={this.saveRate} disabled={!score}>送出</Button>
              <Box mt="0.25em">
                <Button.transparent onClick={this.onClose}>下次再說</Button.transparent>
              </Box>
            </Fragment>
          )}
        </Box>
      </Modal>
    );
  }
}

export default ({ rateKey, ...props }) => createElement(rateHoc(rateKey)(StarRater), props);
