import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import { createSelector, createStructuredSelector } from 'reselect'

import { saveRate } from './rateReducer';

const mapStateToProps = key => (state) => ({
  score: state.rates.get(key),
});

const mapDispatchToProps = key => (dispatch) => bindActionCreators({
  saveRate: rate => saveRate({ rate, key })
}, dispatch);

export default key => connect(mapStateToProps(key), mapDispatchToProps(key));
